.App {
  font-family: "KimjungchulMyungjo";
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 1000px;
  align-items: center;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  width: 500px;
  background-color: #f6f6f6;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.3);
}

.container-line {
  width: 100%;
  height: 10px;
  border-radius: 10px 10px 0 0;
  background-color: #2f92a4;
}

.blog-title {
  margin: 10px auto 5px;
  font-size: 18px;
  font-weight: 600;
}

.text-line {
  width: 130px;
  margin: 0 auto 15px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.post-input {
  width: 85%;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.3);
  padding: 15px;
  background: white;
}

.post-input-title,
.post-input-context {
  width: 95%;
  padding-left: 5px;
  border-radius: 5px;
  border: 1.5px solid #ebecec;
  margin: 0px auto 5px;
}

.post-input-title {
  height: 30px;
  font-size: 14px;
}

.post-input-context {
  height: 80px;
  font-size: 12px;
}

.upload-btn {
  width: 80px;
  background-color: #2f92a4;
  color: white;
  font-size: 12px;
  font-family: "KimjungchulMyungjo";
  font-weight: 600;
  border: #2f92a4;
  padding: 4px;
  border-radius: 5px;
  margin-left: 6px;
}

.post-list {
  width: 85%;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.3);
  padding: 15px;
  background: white;
  margin: 20px auto;
}

.post {
  width: 90%;
  padding: 20px;
}

.post-line {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  margin: 10px auto 15px;
}

.post-title {
  display: block;
  font-size: 13px;
  margin: 3px 2px;
}

.post-content {
  font-size: 11px;
  margin: 5px 2px 20px;
}

.post-date,
.post-good,
.btn-wrapper {
  font-size: 10px;
  text-align: right;
  margin: 0px 20px 5px 0px;
}

.edit-btn,
.done-btn,
.delete-btn {
  width: 40px;
  background-color: #2f92a4;
  color: white;
  font-size: 10px;
  font-family: "KimjungchulMyungjo";
  font-weight: 600;
  border: #2f92a4;
  padding: 3px;
  border-radius: 5px;
  margin-left: 6px;
}

/* 서체 모음 */
@font-face {
  font-family: "KimjungchulGothic";
  src: url("./fonts/KimjungchulGothic-Light.ttf");
}

@font-face {
  font-family: "KimjungchulMyungjo";
  src: url("./fonts/KimjungchulMyungjo-Light.ttf");
}

@font-face {
  font-family: "KimjungchulScript";
  src: url("./fonts/KimjungchulScript-Light.ttf");
}
